.inmuebles{
    @include grid(2,2rem);

    .inmueble{
        border: 2px solid $blanco-gris;
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        // padding-bottom: 1rem;
        img{
            // border: 1px solid black;
            width: 100%;
            height: auto;
            border-radius: 1rem;
            margin-bottom: 1rem;
        }
        transition: all 300ms ease;
            &:hover{
                cursor: pointer;
                transform: scale(1.05);
            }
        .info-inmueble{
            // border: 1px solid black;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            .direccion,.precio,.pago,.estado{
                margin: 0 auto;
                text-align: center;
                font-size: 1.4rem;
            }
            .direccion{
                color: $gris;
            }
            
            .estado{
                color: red;
            }
        }
    }
    @include desktop{
        @include grid(4,2rem);
    }
}

.buscador{
    margin: 1rem auto;
}

.beneficios{
    margin:1rem 0;
    @include grid(2,1rem);
    // display: flex;
    // justify-content: space-between;
    width: 100%;
    .beneficio{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
        // border-right: 1px solid $gris;
        &:last-of-type{
            border-right: none;
        }
        img{
            height: 35%;
            width: 35%;
            margin: auto;
            border-radius: 0;
        }
        p{
            font-size: 1.2rem;
            margin: auto 0.8rem; 
        }
        // @include tablet{
        //     img{
        //         width: 3rem;
        //     }
        // }
    }
}

.precio{
    color: $verde;
}

.opciones{
    margin-top: 1.4rem;
    
    @include grid(2,0.5rem);
    a{
        font-size: 1rem;
        width: 100%;
        height: auto;
        margin: auto;
        color: $negro;
        &:hover{
            color: $blanco;
        }
    }
    @include tablet{
        a{
            font-size: 1.4rem;
        }
    }
}