.trabajadores{
    @include grid(2,1rem);
    .trabajador{
        padding: 5%;
        border-radius: 0.5rem;
        border: 1px solid $gris;
        
        transition: all 300ms ease;
        &:hover{
            transform: scale(1.05);
        }
        p{
            text-align: center;
            line-height: 1rem;
            font-size: 1.2rem;
        }
    }
    @include tablet{
        @include grid(3,4rem);
        .trabajador{
            p{
                font-size: 1.6rem;
                line-height: 1.2rem;
            }
        }
    }
}